/**
 * vue的插件，用来获取和设置localStorage存储
 */
let local = {
  save (key, value) {
    localStorage.setItem(key, JSON.stringify(value))
  },
  fetch (key) {
    if (localStorage && localStorage.getItem(key)) {
      return JSON.parse(localStorage.getItem(key))
    } else {
      return {}
    }
  }
}

export default {
  install: function (vm) {
    vm.prototype.$local = local
  }
}



